<template>
   <div style="padding:20px">
    <v-row>
      <v-btn @click="onClickRefresh" x-small color="primary" class="white--text">Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn >&nbsp;&nbsp;
      <v-btn @click="dialogDevice = true" x-small color="success" class="white--text">Add Devices<v-icon x-small right dark> mdi-plus </v-icon> </v-btn>&nbsp;&nbsp;
      <v-btn x-small color="primary" @click="clickImportJsonDevices" >Import Devices&nbsp;<v-icon small>mdi-import</v-icon></v-btn>&nbsp;&nbsp;
      <v-btn x-small @click="clickJsonExportDevices" color="#009688" class="white--text">Export Device&nbsp;<v-icon small> mdi-export </v-icon></v-btn>&nbsp;&nbsp;
      <v-btn @click="advancedView = !advancedView" x-small color="purple" class="white--text" > Advanced View <v-icon x-small right dark> {{advancedView ? 'mdi-playlist-minus': 'mdi-playlist-plus'}} </v-icon> </v-btn>&nbsp;&nbsp;

    </v-row><br/>

    <v-expansion-panels v-model="expansionData" multiple>
      <v-expansion-panel v-for="(device,di) in devices" :key="di" class="mb-1">
        <v-expansion-panel-header>
          <v-row>
            <h3> {{ device.name }} </h3>&nbsp;&nbsp;
            <v-icon @click.stop="clickEditDevice(device)" color="info" right dark small> mdi-pencil </v-icon>&nbsp;
            <v-icon @click.stop="clickDeleteDevice(device)" color="error" right dark small> mdi-delete </v-icon>&nbsp;
          </v-row>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-data-table
            v-model="selected"
            :headers="advancedView ? advancedHeaders : pointHeaders"
            :items="device.points"
            class="elevation-1 text-left"
            item-key="_id"
            show-select
            :item-class="backgroundColor"
            :footer-props="{
              showFirstLastPage: true,
              firstIcon: 'mdi-chevron-double-left',
              lastIcon: 'mdi-chevron-double-right',
              prevIcon: 'mdi-chevron-left',
              nextIcon: 'mdi-chevron-right',
            }"
            :search="search"
          >
            <template v-slot:top>
              <v-toolbar flat>
                <v-toolbar-title>POINTS</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <div class="text-left pa-2">
                  <!-- Create btn-->
                  <v-btn @click="dialog = true; editedItem.device_id = device._id; currentDevice = device" x-small color="success" class="white--text"> Create<v-icon x-small right dark> mdi-plus </v-icon> </v-btn >&nbsp;
                  <!-- Delete btn -->
                  <v-btn @click="clickDeleteMulti(device)" :disabled="selected.length === 0" x-small color="error" class="white--text" > Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn>&nbsp;
                  <!-- Download btn -->
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn x-small color="#009688" class="white--text" dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn >
                    </template>
                    <v-list>
                      <v-list-item link>
                        <v-list-item-title @click="clickDownload(device.points, 'json')">Json</v-list-item-title>
                      </v-list-item>
                      <v-list-item link>
                        <v-list-item-title @click="clickDownload(device.points, 'excel')">Excel</v-list-item-title>
                      </v-list-item>
                      <v-list-item link>
                        <v-list-item-title @click="clickDownload(device.points, 'csv')">CSV</v-list-item-title>
                      </v-list-item>
                    </v-list>
                  </v-menu >&nbsp;
                  <!-- Import btn -->
                  <v-menu offset-y>
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on" >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn >
                    </template>
                    <v-list>
                      <v-list-item link>
                        <v-list-item-title @click="clickImportJson(device)">Json</v-list-item-title>
                      </v-list-item>
                      <v-list-item link>
                        <v-list-item-title @click="clickImportExcel(device)" >Excel</v-list-item-title >
                      </v-list-item>
                    </v-list>
                  </v-menu>&nbsp;
                  <!-- Export btn -->
                  <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon> </v-btn>&nbsp;

                </div>
                <v-spacer></v-spacer>
                <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
              </v-toolbar>
            </template>

            <template v-slot:item.enable="{ item }">
              <v-checkbox v-model="item.enable" @click="updatePoint(item)"></v-checkbox>
            </template>

            <template v-slot:item.writable="{ item }">
              <v-checkbox v-model="item.writable" @click="updatePoint(item)"></v-checkbox>
            </template>

            <template v-slot:item.created_on="{ item }" >
              {{item.created_on | timezone}}
            </template>

            <template v-slot:item.updated_on="{ item }" >
              {{item.updated_on | timezone}}
            </template>

            <template v-slot:item.register="{ item }">
              <v-edit-dialog :return-value.sync="item.register" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.register }}</b>
                <template v-slot:input>
                  <v-text-field v-model="item.register" single-line type='number' min="0" @input="numberChange()" ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.register_length="{ item }">
              <v-edit-dialog :return-value.sync="item.register_length" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.register_length }}</b>
                <template v-slot:input>
                  <v-text-field v-model="item.register_length" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.function_code="{ item }">
              <v-edit-dialog :return-value.sync="item.function_code" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.function_code }}</b>
                <template v-slot:input>
                  <v-select v-model="item.function_code" :items="point_types" ></v-select>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.data_type="{ item }">
              <v-edit-dialog :return-value.sync="item.data_type" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.data_type }}</b>
                <template v-slot:input>
                  <v-select v-model="item.data_type" :items="point_data_types"></v-select>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.data_endian="{ item }">
              <v-edit-dialog :return-value.sync="item.data_endian" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.data_endian }}</b>
                <template v-slot:input>
                  <v-select v-model="item.data_endian" :items="point_data_endians"></v-select>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.value_round="{ item }">
              <v-edit-dialog :return-value.sync="item.value_round" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.value_round }}</b>
                <template v-slot:input>
                  <v-text-field v-model="item.value_round" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.value_offset="{ item }">
              <v-edit-dialog :return-value.sync="item.value_offset" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.value_offset }}</b>
                <template v-slot:input>
                  <v-text-field v-model="item.value_offset" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.value_raw="{ item }">
              <v-edit-dialog :return-value.sync="item.value_raw" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.value_raw }}</b>
                <template v-slot:input>
                  <v-text-field v-model="item.value_raw" single-line></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.value_operation="{ item }">
              <v-edit-dialog :return-value.sync="item.value_operation" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.value_operation }}</b>
                <template v-slot:input>
                  <v-select v-model="item.value_operation" :items="value_operation" ></v-select>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.write_value="{ item }">
              <v-edit-dialog :return-value.sync="item.write_value" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.write_value }}</b>
                <template v-slot:input>
                  <v-text-field v-model="item.write_value" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.cov_threshold="{ item }">
              <v-edit-dialog :return-value.sync="item.cov_threshold" @save="updatePoint(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                <b>{{ item.cov_threshold }}</b>
                <template v-slot:input>
                  <v-text-field v-model="item.cov_threshold" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                </template>
              </v-edit-dialog>
            </template>

            <template v-slot:item.actions="{ item }">
              <v-icon small class="mr-2" @click="currentDevice = device; editItem(item)" color="primary"> mdi-pencil </v-icon>
              <v-icon small @click="currentDevice = device; clickDelete(item)" color="error"> mdi-delete </v-icon>
            </template>

            <!-- header -->
            <template v-slot:header.enable="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.writable="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('writable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('writable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.register_length="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('register_length') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('register_length')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.data_type="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('data_type') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('data_type')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.data_endian="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('data_endian') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('data_endian')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.value_raw="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('value_raw') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('value_raw')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.value_round="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('value_round') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('value_round')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.value_offset="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('value_offset') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('value_offset')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.value_operation="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('value_operation') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('value_operation')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.write_value="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('write_value') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('write_value')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:header.cov_threshold="{ item }">
                <div class="d-flex">
                  <span>{{ toUpperTextKey('cov_threshold') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('cov_threshold')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

            <template v-slot:no-data> No data </template>
          </v-data-table>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels> 

    <!-- Create/Edit device dialog-->
    <v-dialog v-model="dialogDevice" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formDeviceTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12">
                  <v-text-field v-model="editedDevice.name" label="device name" required :rules="[() => !isExistingDevicevalue(editedDevice.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDevice">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="saveDevice(editedDevice)" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import device wizard dialog-->
    <v-dialog v-model="dialogImportDevice" max-width="800px">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Devices Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImportDevice">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1Device">
        <v-stepper-header>
          <v-stepper-step :complete="e1Device > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1Device > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImportDevices"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinueImportDevice()" :disabled="!dataImportDevices"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="deviceHeaders"
                :items="importArrayDevice"
                class="elevation-1 mb-4"
                item-key="_id"
                :footer-props="{
                  showFirstLastPage: true,
                  firstIcon: 'mdi-chevron-double-left',
                  lastIcon: 'mdi-chevron-double-right',
                  prevIcon: 'mdi-chevron-left',
                  nextIcon: 'mdi-chevron-right',
                }"
                :search="searchWizardDevice"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>Import Devices Table</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizardDevice" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>

                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImportDevice(item)" v-if="!isClickSaveImportDevice"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImportDevice && !isExistingDevicevalue(item._id, '_id')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="saveDevice(item)" v-if="isClickSaveImportDevice" :disabled="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.name="{ item }">
                  <v-text-field v-if="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.name" single-line required :rules="[() => !isExistingDevicevalue(item.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1Device = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImportDevice" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Delete Device dialog -->
    <v-dialog v-model="dialogDeleteDevice" persistent max-width="300px">
      <v-card>
        <v-card-title> Confirm Delete Device</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure delete&nbsp;<b> {{currentDevice.name}}</b>?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDeleteDevice"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirmDeleteDevice"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="pointHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="{
                  showFirstLastPage: true,
                  firstIcon: 'mdi-chevron-double-left',
                  lastIcon: 'mdi-chevron-double-right',
                  prevIcon: 'mdi-chevron-left',
                  nextIcon: 'mdi-chevron-right',
                }"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.uuid, 'uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createPoint(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.name="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.register="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.register" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.register" type="number" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.register, 'register') || 'Register Existed', rules.required]" ></v-text-field>
                </template>
                <template v-slot:item.created_on="{ item }" >
                  {{item.created_on | timezone}}
                </template>
                <template v-slot:item.updated_on="{ item }" >
                  {{item.updated_on | timezone}}
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <v-dialog v-model="dialogImportExcel" width="500px">
      <v-card>
        <v-card-title>Choose excel file</v-card-title>
        <v-card-text>
          <v-file-input outlined dense label="File input" v-model="file" accept=".xls,.xlsx"></v-file-input>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogImportExcel = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="convertExcelToJson" :disabled="!file">Convert</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete dialog -->
    <v-dialog v-model="dialogDelete" persistent max-width="290">
      <v-card>
        <v-card-title> Confirm Delete Point</v-card-title>
        <v-card-text>
          <v-container>
            <v-row>Are you sure?</v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDelete"> No </v-btn>
          <v-btn color="red darken-1" text @click="confirm"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!--Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Point</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="currentDevice.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" label="Point name" required :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.cov_threshold" label="cov threshold" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.function_code" label="function_code" :items="point_types" required :rules="[rules.required]" v-on:change="$refs.form.resetValidation()" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.data_type" label="data type" :items="point_data_types" required :rules="[rules.required]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.data_endian" label="data endian" :items="point_data_endians" required :rules="[rules.required]" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.register" label="register" :rules="[() => !isExistingvalue(editedItem.register, 'register', editedItem) || 'Register Existed']" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.register_length" label="register length" type="number" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.writable" label="writable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.write_value" label="write value" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point round/math" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_round" label="value round" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_offset" label="value offset" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.value_operation" label="value operation" :items="value_operation" ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.value_raw" label="value raw"></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field  value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-select v-if="editField === 'data_type'" v-model="editAll[editField]" :items="point_data_types" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'data_endian'" v-model="editAll[editField]" :items="point_data_endians" :label="toUpperTextKey(editField)" ></v-select>
          <v-select v-if="editField === 'value_operation'" v-model="editAll[editField]" :items="value_operation" :label="toUpperTextKey(editField)" ></v-select>
          <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
          <v-text-field v-if="typeof editAll[editField] === 'number'" v-model.number="editAll[editField]" min="0" :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
          <v-text-field v-if="typeof editAll[editField] === 'string' && editField !== 'function_code' && editField !== 'data_type' && editField !== 'data_endian' && editField !== 'value_operation'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </div>
</template>

<script>
import axios from '../../axios';
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey } from '../../utils/utils';
import * as xlsx from 'xlsx';

export default {
  components: {
  },
  data: () => ({
    devices: [],
    importArrayDevice: [],
    expansionData: [],
    deviceHeaders: [
      { text: 'Actions', value: 'actions', },
      { text: 'Name', value: 'name', align: 'start' },
    ],
    searchWizardDevice: '',
    dataImportDevices: '',
    valid: false,
    dialogDevice: false,
    dialogDeleteDevice: false,
    dialogImportDevice: false,
    isClickSaveImportDevice: false,
    e1Device: 1,
    editedDeviceIndex: -1,
    editedDevice: {},
    currentDevice: {},
    rules: {
      required: value => !!value || 'Required.',
    },
    advancedView: false,
    pointHeaders: [
      { text: 'Name', value: 'name', width: '90px' },
      { text: 'Enable', value: 'enable', sortable: false },
      { text: 'COV threshold', value: 'cov_threshold', sortable: false },
      { text: 'Function code', value: 'function_code', sortable: false },
      { text: 'Value', value: 'value', sortable: false },
      { text: 'Updated on', value: 'updated_on', sortable: false },
    ],
    advancedHeaders: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Name', value: 'name', width: '90px' },
      { text: 'Driver', value: 'driver', sortable: false },
      { text: 'Enable', value: 'enable', sortable: false },
      { text: 'COV threshold', value: 'cov_threshold', sortable: false },
      { text: 'Function code', value: 'function_code', sortable: false },
      { text: 'Register', value: 'register', sortable: false },
      { text: 'Register length', value: 'register_length', sortable: false },
      { text: 'Data type', value: 'data_type', sortable: false },
      { text: 'Data endian', value: 'data_endian', sortable: false },
      { text: 'Value', value: 'value', sortable: false },
      { text: 'Value original', value: 'value_original', sortable: false },
      { text: 'Value round', value: 'value_round', sortable: false },
      { text: 'Value offset', value: 'value_offset', sortable: false },
      { text: 'Value operation', value: 'value_operation', sortable: false },
      { text: 'Value raw', value: 'value_raw', sortable: false },
      { text: 'Writable', value: 'writable', sortable: false },
      { text: 'Write value', value: 'write_value', sortable: false },
      { text: 'Fault', value: 'fault', sortable: false },
      { text: 'Fault message', value: 'fault_message', sortable: false },
      { text: 'Fault timestamp', value: 'ts_fault', sortable: false },
      { text: 'Last value change timestamp', value: 'ts_value', sortable: false },
      { text: 'Device ID', value: 'device_id', sortable: false },
      { text: 'Input min', value: 'input_min', sortable: false },
      { text: 'Input max', value: 'input_max', sortable: false },
      { text: 'Scale min', value: 'scale_min', sortable: false },
      { text: 'Scale max', value: 'scale_max', sortable: false },
      { text: 'Created on', value: 'created_on', sortable: false },
      { text: 'Updated on', value: 'updated_on', sortable: false },
      { text: 'ID', value: '_id', align: 'start', sortable: false },
    ],
    point_types: [
      'READ_COILS',
      'READ_DISCRETE_INPUTS',
      'READ_HOLDING_REGISTERS',
      'READ_INPUT_REGISTERS',
      'WRITE_COIL',
      'WRITE_REGISTER',
      'WRITE_COILS',
      'WRITE_REGISTERS',
    ],
    point_data_types: ['RAW', 'INT16', 'UINT16', 'INT32', 'UINT32', 'FLOAT', 'DOUBLE', 'DIGITAL'],
    point_data_endians: ['LEB_BEW', 'LEB_LEW', 'BEB_LEW', 'BEB_BEW'],
    value_operation: ['ADD', 'SUBTRACT', 'MULTIPLY', 'DIVIDE', 'BOOL_INVERT'],
    history_data_types: ['INTERVAL', 'COV'],
    editedItem: {
      name: '',
      register: 0,
      register_length: 1,
      function_code: 'READ_HOLDING_REGISTERS',
      enable: true,
      write_value: 0,
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'ADD',
      input_min: null,
      input_max: null,
      scale_min: null,
      scale_max: null,
      cov_threshold: 0,
      history_enable: false,
      history_type: 'INTERVAL',
      history_interval: 15,
      device_id: '',
    },
    defaultItem: {
      name: '',
      register: 0,
      register_length: 1,
      function_code: 'READ_HOLDING_REGISTERS',
      enable: true,
      write_value: 0,
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'ADD',
      input_min: null,
      input_max: null,
      scale_min: null,
      scale_max: null,
      cov_threshold: 0,
      history_enable: false,
      history_type: 'INTERVAL',
      history_interval: 15,
      device_id: '',
    },
    editedIndex: -1,
    selected:[],
    search: '',
    action:'',
    dialog: false,
    dialogDelete: false,
    currentItem: {},
    searchWizard: '',
    dataImport: '',
    importArray: [],
    dialogImport: false,
    e1: 1,
    dialogImportExcel: false,
    file: null,
    isClickSaveImport: false,
    editAll: {
      enable: false,
      register_length: 0,
      function_code: "READ_HOLDING_REGISTERS",
      data_type: 'RAW',
      data_endian: 'BEB_LEW',
      value_round: 1,
      value_offset: 0,
      value_operation: 'ADD',
      value_raw: '',
      writable: false,
      write_value: 0,
      cov_threshold: 0,
      history_enable: false,
      history_type: 'INTERVAL',
      history_interval: 15,
    },
    editField: "",
    dialogMassEdit: false,
  }),
  computed: {
    formDeviceTitle() {
      return this.editedDeviceIndex === -1 ? 'Create Device' : 'Edit Device';
    },
  },
  watch: {
    devices(val, oldVal) {
      if(val != oldVal){
        this.expansionData = [...val.keys()].map((k, i) => i);
      }
    },
  },
  mounted() {
    this.getdevices();
  },
  methods: {
    //devices
    getdevices() {
      axios.get('modbus').then(r => {
        this.devices = r;
        this.devices.forEach(device=>{
          if(device.points){
            device.points = device.points.map(item => {
              if(item.point_store){
                const keys = Object.keys(item.point_store);
                if (keys && keys.length > 0) {
                  keys.forEach(key => {
                    item = { ...item, [key]: item.point_store[key] };
                  });
                }
                delete item.point_store;
                delete item.point_id;
              }
              return item;
            });
          }
        });
      })
    },
    async clickJsonExportDevices() {
      const status = copyToClipboard(JSON.stringify(this.devices));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickImportJsonDevices() {
      this.dialogImportDevice = true;
      this.dataImportDevices = '';
      this.e1Device = 1;
    },
    async clickDeleteImportDevice(item) {
      const index = this.importArrayDevice.indexOf(item);
      this.importArrayDevice.splice(index, 1);
    },
    async saveImportDevice() {
      if (this.importArrayDevice && this.importArrayDevice.length > 0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImportDevice = true;
          try{
            const promises = [];
            for (const item of this.importArrayDevice) {
              promises.push(new Promise((resolve, reject) => this.saveDevice(item, resolve, reject)));
            }
            await Promise.all(promises);
            this.closeDialogImportDevice();
          }catch(ex){}
        }
      }
    },
    closeDialogImportDevice() {
      this.dialogImportDevice = false;
      this.isClickSaveImportDevice = false;
      this.$nextTick(() => {
        this.dataImportDevices = '';
      });
    },
    clickContinueImportDevice() {
      if (!isJsonString(this.dataImportDevices)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArrayDevice = JSON.parse(this.dataImportDevices).map(device=>{
        delete device._id;
        return device;
      });
      this.e1Device = 2;
    },
    isExistingDevicevalue(value, fieldName) {
      const devices = [...this.devices];
      if (this.editedDeviceIndex !== -1) { //when edit
        devices.splice(this.editedDeviceIndex, 1);
      }
      return devices.find(d => d[fieldName] == value) != null;
    },
    closeDevice() {
      this.dialogDevice = false;
      this.$nextTick(() => {
        this.editedDevice = Object.assign({}, {});
        this.editedDeviceIndex = -1;
      });
    },
    async saveDevice(data, resolve, reject) {
      try {
        const method = data._id ? 'put': 'post';
        const points = data.points ? data.points : [];
        delete data.points;
        const response = await axios[method]('modbus', data);
        if(data._id){
          Object.assign(this.devices[this.editedDeviceIndex], response);
          this.$toasted.show('update success');
        }else{
          this.devices.push(response);
          this.$toasted.show('create success');
          if (points.length > 0) {
            for (const point of points) {
              point.device_id = response._id;
              point.register = point.register ? point.register : 0;
              point.register_length = point.register_length ? point.register_length : 0;
              point.enable = point.enable ? point.enable : false;
              delete point._id;
              this.createPoint(point);
            }
          }
        }
        if (this.importArrayDevice && this.importArrayDevice.length > 0) {
          const index = this.importArrayDevice.findIndex(n => n.name == response.name);
          if(index > -1){
            Object.assign(this.importArrayDevice[index], response);
          }
        }
        this.closeDevice();
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    async clickDeleteDevice(item) {
      this.dialogDeleteDevice = true;
      this.currentDevice = item;
    },
     closeDeleteDevice() {
      this.dialogDeleteDevice = false; 
      this.$nextTick(() => {
        this.currentDevice = {};
      });
    },
    async confirmDeleteDevice() {
      const response = await axios.delete(`modbus/${this.currentDevice._id}`);
      if(response){
        const index = this.devices.indexOf(this.currentDevice);
        this.devices.splice(index, 1);
        this.closeDeleteDevice();
      }
    },
    clickEditDevice(item) {
      this.editedDeviceIndex = this.devices.indexOf(item);
      this.editedDevice = Object.assign({}, item);
      this.dialogDevice = true;
    },
    onClickRefresh(){
      this.getdevices();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    //end devices

    //point
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti(device) {
      this.currentDevice = device;
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      await axios.delete(`modbus/point/${this.currentItem._id}`);
      this.editedDeviceIndex = this.devices.indexOf(this.currentDevice);
      this.editedIndex = this.devices[this.editedDeviceIndex].points.indexOf(this.currentItem);
      this.devices[this.editedDeviceIndex].points.splice(this.editedIndex, 1);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      if (this.$refs.form.validate()) {
        if(this.editedItem._id){
          this.updatePoint(this.editedItem);
        }else{
          this.createPoint(this.editedItem)
        }
      }
    },
    async updatePoint(point) {
      const response = await axios.put('modbus/point', point);
      const { deviceIndex, pointIndex } = this.getIndexs(point);
      Object.assign(this.devices[deviceIndex].points[pointIndex], response);
      this.$toasted.show('update success');
      this.close();
    },
    async createPoint(point){
      const response = await axios.post('modbus/point', point);
      const { deviceIndex } = this.getIndexs(point);
      this.devices[deviceIndex].points = this.devices[deviceIndex].points ? [...this.devices[deviceIndex].points] : [];
      this.devices[deviceIndex].points.push(response);
      this.$toasted.show('create success');
      this.close();
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImport = true;
          try{
            for (const item of this.importArray) {
              item.device_id = this.currentDevice._id;  //update to the current device_id
              item.register_length = item.register_length ? item.register_length : 0;
              if(!item._id){
                this.createPoint(item);
              }
            }
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){}
        }
      }
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport= false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    clickImportJson(device) {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
      this.editedDeviceIndex = this.devices.indexOf(device);
      this.currentDevice = device;
    },
    clickImportExcel(device) {
      this.dialogImportExcel = true; 
      this.file = null;
      this.editedDeviceIndex = this.devices.indexOf(device);
      this.currentDevice = device;
    },
    convertExcelToJson() {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(this.file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.device_id = this.currentDevice._id;
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point=>{
        delete point._id;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName, item) {
      if(!this.devices || (this.devices && this.devices.length == 0) ) return false;
      const deviceIndex = this.devices.indexOf(this.currentDevice);
      let points =this.devices[deviceIndex].points ? [...this.devices[deviceIndex].points] : [];
      if(points && points.length == 0) return false;
      if (this.editedIndex !== -1) {
        //when edit
        points.splice(this.editedIndex, 1);
      }
      if (fieldName === 'register') {
        value = Number(value);
        if (item) {
          points = points.filter(p => p.function_code === item.function_code);
        }
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      if (type === 'csv') {
        return exportCSV('points', array);
      }
      if (type === 'excel') {
        return exportExcel('points', array);
      }
      if (type === 'json') {
        return exportJSON('points', JSON.stringify(array));
      }
    },
    editItem(item) {
      this.editedDeviceIndex = this.devices.indexOf(this.currentDevice);
      this.editedIndex = this.devices[this.editedDeviceIndex].points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      this.editAll = {
        enable: false,
        register_length: 0,
        function_code: "READ_HOLDING_REGISTERS",
        data_type: 'RAW',
        data_endian: 'BEB_LEW',
        value_round: 1,
        value_offset: 0,
        value_operation: 'ADD',
        value_raw: '',
        writable: false,
        write_value: 0,
        cov_threshold: 0,
        history_enable: false,
        history_type: 'INTERVAL',
        history_interval: 15
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    getIndexs(point) {
      const deviceIndex = this.devices ? this.devices.findIndex(d => d._id== point.device_id) : -1;
      const pointIndex = (deviceIndex > -1 && this.devices[deviceIndex].points) ? this.devices[deviceIndex].points.findIndex(p=>p._id == point._id) : -1;
      return { deviceIndex, pointIndex };
    },
    
    //end point
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
  },
};
</script>
<style>
.background-fault-true {
  background-color: #fbb93e;
}
.background-enable-false {
  background-color: #666666;
}
</style>




