<template>
  <v-dialog v-model="isOpen" persistent>
    <v-card>
      <v-card-title><span class="headline">Import Mappings</span></v-card-title>
      <v-card-text>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-data-table
            :headers="headers"
            :items="dataItems"
            class="elevation-1 mb-4"
            :footer-props="{
              showFirstLastPage: true,
              firstIcon: 'mdi-chevron-double-left',
              lastIcon: 'mdi-chevron-double-right',
              prevIcon: 'mdi-chevron-left',
              nextIcon: 'mdi-chevron-right',
            }"
          >
            <template v-slot:item.actions="{ item }">
              <v-icon small @click="clickDeleteImport(item.index)" color="error"> mdi-delete </v-icon>
            </template>
            <template v-slot:item.rowNumber="{ item, index }">
              <span>{{ +index + 1 }}</span>
            </template>
            <template v-slot:item.modbusNetworkRTUPort="{ item }">
              <v-combobox
                v-model="item.modbus_point.network.rtu_port"
                :items="modbusRtuPorts"
                label="rtu port"
              ></v-combobox>
            </template>
            <template v-slot:item.modbusNetworkName="{ item }">
              <v-text-field
                v-model="item.modbus_point.network.name"
                single-line
                style="width: 100px"
                required
              ></v-text-field>
            </template>
            <template v-slot:item.modbusDeviceName="{ item }">
              <v-text-field
                v-model="item.modbus_point.device.name"
                single-line
                style="width: 100px"
                required
              ></v-text-field>
            </template>
            <template v-slot:item.modbusDeviceAddress="{ item }">
              <v-text-field v-model="item.modbus_point.device.address" single-line type="number" min="0"></v-text-field>
            </template>
            <template v-slot:item.modbusPointName="{ item }">
              <v-text-field
                v-model="item.modbus_point.point.name"
                single-line
                style="width: 100px"
                :rules="[rules.required]"
                required
              ></v-text-field>
            </template>
            <template v-slot:item.modbusPointRegister="{ item }">
              <v-text-field v-model="item.modbus_point.point.register" single-line type="number" min="0"></v-text-field>
            </template>
            <template v-slot:item.genericNetworkName="{ item }">
              <v-text-field
                :disabled="item.isGenericPointDisabled"
                v-model="item.generic_point.network.name"
                single-line
                style="width: 100px"
              ></v-text-field>
            </template>
            <template v-slot:item.genericDeviceName="{ item }">
              <v-text-field
                :disabled="item.isGenericPointDisabled"
                v-model="item.generic_point.device.name"
                single-line
                style="width: 100px"
              ></v-text-field>
            </template>
            <template v-slot:item.genericPointName="{ item }">
              <v-text-field
                :disabled="item.isGenericPointDisabled"
                v-model="item.generic_point.point.name"
                single-line
                style="width: 100px"
              ></v-text-field>
            </template>
            <template v-slot:item.bacnetPointName="{ item }">
              <v-text-field
                :disabled="item.isBacnetPointDisabled"
                v-model="item.bacnet_point.name"
                single-line
                style="width: 100px"
              ></v-text-field>
            </template>
            <template v-slot:item.bacnetPointAddress="{ item }">
              <v-text-field
                :disabled="item.isBacnetPointDisabled"
                v-model="item.bacnet_point.address"
                single-line
                style="width: 100px"
              ></v-text-field>
            </template>
          </v-data-table>
        </v-form>
      </v-card-text>
      <v-card v-if="errors.length >= 1" class="px-8 mt-n15">
        <v-card-title color="red darken-1">Errors</v-card-title>
        <v-card-text>
          <v-data-table hide-default-footer :headers="errorTableHeaders" :items="errors" class="elevation-1 mb-1">
          </v-data-table>
        </v-card-text>
      </v-card>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="closeDialog"> Cancel </v-btn>
        <v-btn color="blue darken-1" :loading="isCreatingMapping" text @click="createMapping"> Save </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import * as modbusBacnetGenericPointsService from '../../services/modbusBacnetGenericPointsService';
import { MODBUS_RTU_PORTS } from '../../constants/appConstant';
export default {
  name: 'modus-generic-bacnet-point-mapping-import-dialog',
  props: ['dataRows', 'isOpen', 'close', 'bacnetPoints', 'genericPoints', 'modusPoints', 'syncData'],
  data: () => ({
    valid: false,
    isRequestOnce: false,
    isCreatingMapping: false,
    errors: [],
    rules: { required: value => !!value || 'Required' },
    errorTableHeaders: [
      { text: 'Row', value: 'rowIndex', sortable: false },
      { text: 'Error Message', value: 'message', sortable: false },
    ],
    modbusRtuPorts: [...MODBUS_RTU_PORTS],
    headers: [
      { text: 'Actions', value: 'actions', sortable: false },
      { text: 'Row', value: 'rowNumber', sortable: false },
      { text: 'Modbus Network Name', value: 'modbusNetworkName', sortable: false },
      { text: 'Modbus Network RTU Port', value: 'modbusNetworkRTUPort', sortable: false },
      { text: 'Modbus Device Name', value: 'modbusDeviceName', sortable: false },
      { text: 'Modbus Device Address', value: 'modbusDeviceAddress', sortable: false },
      { text: 'Modbus Point Name', value: 'modbusPointName', sortable: false },
      { text: 'Modbus Point Regiter', value: 'modbusPointRegister', sortable: false },
      { text: 'Generic Network Name', value: 'genericNetworkName', sortable: false },
      { text: 'Generic Device Name', value: 'genericDeviceName', sortable: false },
      { text: 'Generic Point Name', value: 'genericPointName', sortable: false },
      { text: 'Bacnet Point Name', value: 'bacnetPointName', sortable: false },
      { text: 'Bacnet Point Address', value: 'bacnetPointAddress', sortable: false },
    ],
  }),
  computed: {
    dataItems() {
      return (this.dataRows || []).map(row => ({
        ...row,
        generic_point: row.generic_point || { point: { name: '' }, device: { name: '' }, network: { name: '' } },
        bacnet_point: row.bacnet_point || { address: null, name: '' },
        isGenericPointDisabled: !row.generic_point,
        isBacnetPointDisabled: !row.bacnet_point,
      }));
    },
  },
  methods: {
    clickDeleteImport(index) {
      this.dataRows = this.dataRows.filter((item, itemIndex) => itemIndex !== index);
      this.errors = this.errors.filter((item, index) => itemIndex !== index);
    },
    getErrorFromResult(result) {
      return {
        rowIndex: result.rowIndex + 1,
        message: result.message,
      };
    },
    createMapping() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.isCreatingMapping = true;
      this.isRequestOnce = true;
      modbusBacnetGenericPointsService
        .createMappingFromFileUpload(this.dataItems)
        .then(results => {
          this.errors = results.reduce((acc, currResult) => {
            if (!currResult.success) {
              acc.push(this.getErrorFromResult(currResult));
            }
            return acc;
          }, []);
          const areAllSuccess = results.map(result => result.success).every(item => item);
          if (areAllSuccess) {
            this.syncData();
            this.closeDialog();
          }
        })
        .catch(err => {})
        .finally(() => {
          this.isCreatingMapping = false;
        });
    },
    closeDialog() {
      if (this.isRequestOnce) {
        this.syncData();
      }
      this.close();
    },
  },
};
</script>
