<template>
  <div style="padding:20px">
    <v-row>
      <v-col class="text-left">
        <v-btn x-small class="text-left" color="primary" @click="clickAddIframe" >Create&nbsp;<v-icon small>mdi-plus</v-icon> </v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small color="primary" @click="clickImport" >Import&nbsp;<v-icon small>mdi-import</v-icon></v-btn>
        <v-divider inset vertical></v-divider>
        <v-btn x-small @click="clickJsonExport" color="#009688" class="white--text">Export&nbsp;<v-icon small>mdi-export</v-icon></v-btn>
      </v-col>
    </v-row>
    <v-data-table :headers="iframeHeaders" :items="iframes">
      <template v-slot:item.tools="{item}">
        <v-btn @click="() => toggleConfirmDialog(item)" icon small color="error" class="white--text"><v-icon small dark >mdi-delete</v-icon></v-btn>
        <v-btn @click="() => clickUpdateIframe(item)" icon small color="success" class="white--text"><v-icon small dark >mdi-rename-box</v-icon></v-btn>
      </template>
      <template v-slot:item.ip="{item}">{{item.ipType === 'userBrowserIp' ? hostname: item.ip}}</template>
    </v-data-table>
    <!-- dialogIframe -->
    <v-dialog v-model="dialogIframe" max-width="600px">
      <iframe-form v-if="dialogIframe" :onSave="onSaveIframe" :onCancel="onCancelIframe" :item="currentIframe"></iframe-form>
    </v-dialog>
    <!--Confirm dialog-->
    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">Confirm? </v-card-title>
        <v-card-text>Do you want to delete?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="closeDialog" > No </v-btn>
          <v-btn color="red darken-1" text @click="clickDeleteIframe()"> Yes </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--Import dialog -->
    <v-dialog v-model="importDialog" max-width="500px">
      <v-card>
        <v-card-title> <span class="headline">Import</span> </v-card-title>
        <v-card-text>
          <v-alert class="text-left" dense colored-border type="error" elevation="2" v-show="isValidData" >Incorrect Data Type!</v-alert>
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" @input="onChangeImportData" ></v-textarea>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDialogImport">Cancel</v-btn>
          <v-btn color="blue darken-1" :disabled="disableSave" text v-on:click="saveImport">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "../../axios";
import IframeForm from './IframeForm';
import { copyToClipboard , isJsonString, isValidArray } from '../../utils/utils';

const requireKeys=["ip","port","name","type","icon","show","_id"];

export default {
    name: "View",
    components: {
        IframeForm
    },
    data(){
      return{
        currentIframe: {},
        dialogIframe:false,
        hostname: location.hostname,
        choseIframe:{},
        confirmDialog:false,
        importDialog:false,
        dataImport:"",
        isValidData:false,
        disableSave:true,
        iframeHeaders: [
          {
            text: 'Name',
            align: 'start',
            value: 'name',
          },
          { text: 'Icon', value: 'icon' },
          { text: 'IP', value: 'ip' },
          { text: 'Port', value:'port' },
          { text: 'Tools', value: 'tools' },
        ],
      };
    },
    computed: {
      iframes () {
        return this.$store.state.iframes;
      },
    },
    mounted(){
    },
    methods: {
      closeDialogImport(){
        this.importDialog = false;
      },
      clickImport(){
        this.importDialog = true;
      },
      getIframes() {
        axios.get('iframe').then(r => {
          this.$store.commit('changeIframes', r);
        })
      },
      closeDialog(){
        this.confirmDialog = false;
        this.choseIframe={};
      },
      toggleConfirmDialog(data){
        this.choseIframe=data;
        this.confirmDialog = !this.confirmDialog;
      },
      async clickDeleteIframe(data) {
        this.$store.commit('changeLoading', true);
        try{
          await axios.delete(`iframe/${this.choseIframe._id}`);
          this.$store.commit('changeLoading', false);
          this.$toasted.show('Success!');
          this.getIframes();
          this.closeDialog();
        }
        catch(message){
          this.$store.commit('changeLoading', false);
          this.$toasted.show(message, {type: 'error'});
        }
      },
      clickUpdateIframe(data) {
        this.currentIframe = Object.assign({}, data);
        this.dialogIframe= true;
      },
      clickAddIframe(){
          this.currentIframe={};
          this.dialogIframe=true;
      },
      onSaveIframe(data) {
        if(Object.isExtensible(data.icon)){
            Object.assign(data,{icon:data.icon.name});
        }
        this.$store.commit('changeLoading', true);
        const method = data._id ? 'put': 'post';
        axios[method]('iframe', data).then(r => {
          this.$store.commit('changeLoading', false);
          this.$toasted.show('Success!');
          this.dialogIframe = false;
          this.getIframes();
        });
      },
      onCancelIframe() {
          this.dialogIframe = false;
      },
    async onChangeImportData(){
      this.isValidData = this.disableSave= !isJsonString(this.dataImport) || !isValidArray(this.dataImport, requireKeys);
    },
    clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.iframes));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, {type: type});
    },
    async saveImport(){
      const data =JSON.parse(this.dataImport);
      for(let i = 0 ; i < data.length ; i++ ){
        this.$store.commit('changeLoading', true);
        const method = data[i]._id ? 'put': 'post';
        await axios[method]('iframe',data[i]).then(r => {
          this.$store.commit('changeLoading', false);
          this.getIframes();
          this.importDialog = false;
          this.dataImport = '';
        });
      }
    },
  }
}
</script>

