<template>
  <v-card-text>
    <parent-child-mapping-form
      :parentPoints="modbusPoints"
      :child1Points="bacnetPoints"
      :child2Points="genericPoints"
      parentMappingKey="modbusPoint"
      child1MappingKey="bacnetPoint"
      child2MappingKey="genericPoint"
      parentFieldTitle="Modbus Points"
      child1FieldTitle="BACnet Points"
      child2FieldTitle="Rubix Points"
      :serviceMappings="serviceMappings"
      :createServiceMapping="createServiceMapping"
    >
    </parent-child-mapping-form>
    <v-divider></v-divider>
    <parent-child-mapping-grid
      :loading="isFetchingGridData"
      fileName="modus-bacnet-generic-service-mapping"
      :format-data-service="formatExportData"
      :parentPoints="modbusPoints"
      :child1Points="bacnetPoints"
      :child2Points="genericPoints"
      parentMappingKey="modbusPoint"
      child1MappingKey="bacnetPoint"
      child2MappingKey="genericPoint"
      :serviceMappings="serviceMappings"
      :deleteItem="deleteServiceMapping"
      :deleteItems="deleteServiceMappings"
      parentHeaderTitle="Modbus Points"
      child1HeaderTitle="BACnet Points"
      child2HeaderTitle="Rubix Points"
      :updateServiceMapping="updateServiceMapping"
      :receive-data-rows="receiveDataRows"
    ></parent-child-mapping-grid>
    <modbus-generic-bacnet-point-mapping-import-dialog
      :bacnet-points="bacnetPoints"
      :generic-points="genericPoints"
      :modbus-points="modbusPoints"
      :is-open="openImportMappingDialog"
      :close="closeImportMappingDialog"
      :sync-data="syncData"
      :data-rows="dataRows"
    ></modbus-generic-bacnet-point-mapping-import-dialog>
  </v-card-text>
</template>
<script>
import ModbusGenericBacnetPointMappingImportDialog from './ModbusGenericBacnetPointMappingImportDialog';
import ParentChildMappingForm from './ParentChildMappingForm';
import ParentChildMappingGrid from './ParentChildMappingGrid.vue';
import * as pointServerService from '../../services/pointServerService';
import * as modbusBacnetGenericPointsService from '../../services/modbusBacnetGenericPointsService';
import {
  CREATE_MAPPING_SERVICE,
  DELETE_MAPPING_SERVICE,
  DELETE_MAPPING_SERVICES,
  UPDATE_MAPPING_SERVICE,
} from '../../constants/messageConstants';

export default {
  name: 'modbus-generic-bacnet-points-mapping',
  components: {
    ParentChildMappingForm,
    ParentChildMappingGrid,
    ModbusGenericBacnetPointMappingImportDialog,
  },
  data() {
    return {
      dataRows: [],
      isUpdating: false,
      serviceMappings: [],
      genericPoints: [],
      genericPointsMap: {},
      bacnetPoints: [],
      bacnetPointsMap: {},
      modbusPoints: [],
      modbusPointsMap: {},
      isFetchingGridData: false,
      openImportMappingDialog: false,
    };
  },
  mounted() {
    this.syncData();
  },
  methods: {
    syncData() {
      this.fetchServiceMapping();
      this.fetchBacnetPoints();
      this.fetchGenericPoints();
      this.fetchModbusPoints();
    },
    fetchServiceMapping() {
      this.isFetchingGridData = true;
      return modbusBacnetGenericPointsService
        .fetch()
        .then(data => {
          this.serviceMappings = data;
        })
        .finally(() => {
          this.isFetchingGridData = false;
        });
    },
    fetchGenericPoints() {
      pointServerService
        .fetchRubixPSGenericPoints()
        .then(data => {
          this.genericPoints = data;
          this.genericPointsMap = this.createMap(this.genericPoints);
        })
        .catch(err => {});
    },
    fetchBacnetPoints() {
      pointServerService
        .fetchBacnetPoints()
        .then(data => {
          this.bacnetPoints = data.map(item => ({ ...item, id: item.uuid, name: item.object_name }));
          this.bacnetPointsMap = this.createMap(this.bacnetPoints);
        })
        .catch(err => {});
    },
    fetchModbusPoints() {
      pointServerService
        .fetchModbusPoints()
        .then(data => {
          this.modbusPoints = data;
          this.modbusPointsMap = this.createMap(this.modbusPoints);
        })
        .catch(err => {});
    },
    createServiceMapping(mapping) {
      return modbusBacnetGenericPointsService
        .createServiceMapping(mapping)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(CREATE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(CREATE_MAPPING_SERVICE.error, { type: 'error' });
        })
        .finally(() => {});
    },
    updateServiceMapping(mapping, uuid) {
      return modbusBacnetGenericPointsService
        .updateServiceMapping(mapping, uuid)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(UPDATE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(UPDATE_MAPPING_SERVICE.error, { type: 'error' });
        });
    },
    deleteServiceMapping(serviceMapping) {
      return modbusBacnetGenericPointsService
        .deleteServiceMapping(serviceMapping)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(DELETE_MAPPING_SERVICE.success);
        })
        .catch(err => {
          this.$toasted.show(DELETE_MAPPING_SERVICE.error, { type: 'error' });
          console.error(err);
        });
    },
    deleteServiceMappings(serviceMappings) {
      return modbusBacnetGenericPointsService
        .deleteServiceMappings(serviceMappings)
        .then(() => {
          this.fetchServiceMapping();
          this.$toasted.show(DELETE_MAPPING_SERVICES.success);
        })
        .catch(err => {
          this.$toasted.show(DELETE_MAPPING_SERVICES.error, { type: 'error' });
        });
    },
    formatExportData(dataItems, actionType) {
      if (actionType === 'copy') {
        return dataItems
          .map(modbusBacnetGenericPointsService.mapDataToRequest)
          .map(item =>
            modbusBacnetGenericPointsService.appendNetworkDetails(
              item,
              this.modbusPointsMap,
              this.bacnetPointsMap,
              this.genericPointsMap,
            ),
          );
      }
      const data = modbusBacnetGenericPointsService.prepareFileExport(
        dataItems
          .map(modbusBacnetGenericPointsService.mapDataToRequest)
          .map(item =>
            modbusBacnetGenericPointsService.appendNetworkDetails(
              item,
              this.modbusPointsMap,
              this.bacnetPointsMap,
              this.genericPointsMap,
            ),
          ),
      );
      return data;
    },
    createMap(points) {
      return (points || []).reduce((acc, curr) => {
        acc[curr.uuid] = curr;
        return acc;
      }, {});
    },
    receiveDataRows(dataRows = []) {
      if (dataRows.length >= 1) {
        this.dataRows = modbusBacnetGenericPointsService.prepareFileImport(dataRows);
        this.openImportMappingDialog = true;
      }
    },
    closeImportMappingDialog() {
      this.openImportMappingDialog = false;
    },
  },
};
</script>
