<template>
  <div>
    <v-card>
      <v-card-title class="headline">
        Auto refresh
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col cols="12" sm="12" md="12" class="d-flex">
            <div style="margin: auto 0;">Refresh Time:</div>&nbsp;&nbsp;&nbsp;
            <v-radio-group v-model="setting.refreshTime" row >
              <v-radio label="5 sec" value="5000" ></v-radio>
              <v-radio label="15 sec"  value="15000" ></v-radio>
              <v-radio label="30 sec" value="30000" ></v-radio>
              <v-radio label="1 min"  value="60000" ></v-radio>
            </v-radio-group>
          </v-col>
          <v-col cols="12" sm="12" md="12">
            <v-switch v-model="setting.enableAutoRefresh" label="Enable Auto Refresh"></v-switch>
          </v-col>
        </v-row>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="onSave()" :disabled="!setting.enableAutoRefresh && !setting.refreshTime"> Save </v-btn>
      </v-card-actions>
    </v-card>
    <v-card>
      <v-card-title class="headline">
        Timezone
      </v-card-title>
      <v-card-text>
        <v-autocomplete @change="onSave" v-model="setting.timezone" :items="timezones" :label="'Timezones'" persistent-hint></v-autocomplete>
      </v-card-text>
    </v-card>
    <v-card>
      <v-card-title class="headline">
        MQTT Debug
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col :key="field" v-for="field of objectKeys(debugSubscriptions)">
            <h3 class="text-left">
              {{field}}
            </h3>
            <v-row :key="subscription.topic" v-for="subscription of debugSubscriptions[field]">
              <v-col class="text-left d-flex align-center">
                <text-field-display label="Topic name" :value="subscription.name"></text-field-display>
              </v-col>
              <v-col class="text-left d-flex align-center">
                <text-field-display label="Topic" :value="subscription.topic"></text-field-display>
              </v-col>
              <v-col cols="2" class="text-left d-flex align-center">
                <v-switch @change="onSaveMqttConfig" v-model="subscription.enable"></v-switch>
              </v-col>
            </v-row>
          </v-col>
        </v-row>

      </v-card-text>
    </v-card>
  </div>
</template>

<script>
  import axios from "../../axios";
  import {timezones} from '../../constants/appConstant';
  import TextFieldDisplay from '../Common/TextFieldDisplay';
  import MqttConsole from '../Mqtt/MqttConsole';

  export default {
    components: {
      TextFieldDisplay, MqttConsole
    },
    data: () => ({
      timezones,
      objectKeys: Object.keys
    }),
    computed: {
      setting(){
        const result = this.$store.state.setting;
        if (!result.timezone) {
          result.timezone = 'BaseOnBrowser';
        }
        return result;
      },
      mqttConfig() {
        return this.$store.state.mqttConfig;
      },
      debugSubscriptions() {
        const { debugSubscriptions } = this.mqttConfig;
        return debugSubscriptions;
      }
    },
    methods: {
      onSave() {
        axios.put(`setting`, this.setting).then(result => {
          if (result === 'successful') {
            this.$store.commit('changeSetting', this.setting);
            this.$toasted.show('Success!')
          }
        });
      },
      onSaveMqttConfig() {
        axios.put('mqtt/config', this.mqttConfig).then(r => {
          this.$toasted.show('Success');
          this.$store.commit('changeMqttConfig', this.mqttConfig);
        });
      },
    },
  };
</script>
<style>
  .p-relative{
    position: relative;
  }
</style>
