import flat from 'flat';
import http from '../axios';
import _cloneDeep from 'lodash/cloneDeep';
import _get from 'lodash/get';
import _omit from 'lodash/omit';

const endpoint = '/service-mapping/modbus-generic-bacnet-mapping';

function mapDataToView(serviceMappings) {
  return serviceMappings.map(serviceMapping => {
    let serviceMappingO = {};
    if (serviceMapping.generic_point_uuid) {
      serviceMappingO = {
        genericPoint: {
          id: serviceMapping.generic_point_uuid,
          name: serviceMapping.generic_point_name,
        },
      };
    }

    if (serviceMapping.bacnet_point_uuid) {
      serviceMappingO = {
        ...serviceMappingO,
        bacnetPoint: {
          id: serviceMapping.bacnet_point_uuid,
          name: serviceMapping.bacnet_point_name,
        },
      };
    }
    return {
      ...serviceMappingO,
      uuid: serviceMapping.uuid,
      modbusPoint: { id: serviceMapping.modbus_point_uuid, name: serviceMapping.modbus_point_name },
    };
  });
}

export function mapDataToRequest(serviceMapping) {
  let serviceMappingO = {};
  if (serviceMapping.bacnetPoint && serviceMapping.bacnetPoint.id) {
    serviceMappingO = {
      bacnet_point_uuid: serviceMapping.bacnetPoint.id,
      bacnet_point_name: serviceMapping.bacnetPoint.name,
    };
  }
  if (serviceMapping.genericPoint && serviceMapping.genericPoint.id) {
    serviceMappingO = {
      ...serviceMappingO,
      generic_point_uuid: serviceMapping.genericPoint.id,
      generic_point_name: serviceMapping.genericPoint.name,
    };
  }
  if (serviceMapping.uuid) {
    serviceMappingO = {
      ...serviceMappingO,
      uuid: serviceMapping.uuid,
    };
  }
  return {
    ...serviceMappingO,
    modbus_point_uuid: serviceMapping.modbusPoint.id,
    modbus_point_name: serviceMapping.modbusPoint.name,
  };
}

export function appendNetworkDetails(serviceMapping, modbusPointsMap, bacnetPointsMap, genericPointsMap) {
  let networkDetails = {};
  networkDetails.modbus_point = { ...modbusPointsMap[serviceMapping.modbus_point_uuid] };

  if (serviceMapping.bacnet_point_uuid) {
    networkDetails.bacnet_point = { ...bacnetPointsMap[serviceMapping.bacnet_point_uuid] };
  }
  if (serviceMapping.generic_point_uuid) {
    networkDetails.generic_point = { ...genericPointsMap[serviceMapping.generic_point_uuid] };
  }
  networkDetails = _cloneDeep(networkDetails);
  if (networkDetails.modbus_point) {
    delete networkDetails.modbus_point.id;
    delete networkDetails.modbus_point.uuid;
    delete networkDetails.modbus_point.device.uuid;
    delete networkDetails.modbus_point.network.uuid;
    delete networkDetails.modbus_point.point.uuid;
  }

  if (networkDetails.generic_point) {
    delete networkDetails.generic_point.id;
    delete networkDetails.generic_point.uuid;
    delete networkDetails.generic_point.device.uuid;
    delete networkDetails.generic_point.network.uuid;
    delete networkDetails.generic_point.point.uuid;
  }

  if (networkDetails.bacnet_point) {
    delete networkDetails.bacnet_point.id;
    delete networkDetails.bacnet_point.uuid;
  }

  return networkDetails;
}

export function prepareFileExport(dataItems) {
  return dataItems.map(item => flat.flatten(item));
}

export function prepareFileImport(dataItems) {
  return dataItems.map(item =>
    flat.unflatten(item, {
      transformKey: function(key) {
        return key.trim();
      },
    }),
  );
}

export function prepareFileUploadServieMappingRequest(serviceMappings) {
  return serviceMappings.map(serviceMapping => {
    let newServiceMapping = { ...serviceMapping };
    // check generic point exist
    if (
      !(
        _get(serviceMapping, 'generic_point.point.name') &&
        _get(serviceMapping, 'generic_point.point.name') &&
        _get(serviceMapping, 'generic_point.point.name')
      )
    ) {
      newServiceMapping = _omit(newServiceMapping, ['generic_point']);
    }
    if (!_get(serviceMapping, 'bacnet_point.name')) {
      newServiceMapping = _omit(newServiceMapping, ['bacnet_point']);
    }

    return newServiceMapping;
  });
}

export const fetch = () => {
  return http.get(`${endpoint}`).then(mapDataToView);
};

export const createServiceMapping = (serviceMapping = {}) => {
  return http.post(`${endpoint}`, mapDataToRequest(serviceMapping));
};

export const updateServiceMapping = (serviceMapping = {}, uuid) => {
  const requestData = mapDataToRequest(serviceMapping);
  return http.patch(`${endpoint}/${uuid}`, requestData);
};

export const deleteServiceMapping = serviceMapping => {
  return http.delete(`${endpoint}/${serviceMapping.uuid}`);
};

export const createMappingFromFileUpload = serviceMappings => {
  return http.post(`${endpoint}/file-upload`, prepareFileUploadServieMappingRequest(serviceMappings));
};

export const deleteServiceMappings = serviceMappings => {
  return Promise.all(serviceMappings.map(serviceMapping => deleteServiceMapping(serviceMapping)));
};
